body {
  background-color: #9266ff;
}
.app{
  background-color: #3f3160;
  min-height:500px;
  width:400px;
  margin:40px auto;
}

#todoform input{
  background-color: rgb(127,12,112);
  border:0;
  width:200px;
  height:50px;
  padding: 0 20px;
  margin:20px;
  font-size:18px;
  border-radius: 10px;
  color:#fff;
}
#todoform input::placeholder{
  color:rgba(255,255,255,0.5)
}

#todoform input:focus,#todoform button:focus{
  outline: none;  
}

#todoform button{
  height:50px;
  width:80px;
  border-radius: 5px;
  border:0;
  background-color: rgb(202,140,124);
  cursor:pointer;
  font-weight:bolder;
  color:#fff;
}
#todotitle{
  font-size:18px;
  border-radius: 10px;
  color:#8a2df593;
  text-align: center;
  padding-top: 10px;
}